import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { CartHeadAction, ColEditDelete, TableData } from 'components/common/index';
import { Card, Row, Col, message } from 'antd';
import {
  getConvertedPointsPractices,
  deleteConvertedPointsPractices,
} from 'services/convertedPointsPractice';

class ConvertedTrainingPointsPage extends Component {
  state = {
    reload: 0,
  };
  handleDelete = async id => {
    await deleteConvertedPointsPractices(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const columns = [
      {
        title: 'Xp loi rn luyn',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Tn ting anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'T im',
        dataIndex: 'fromGrade',
        key: 'fromGrade',
      },
      {
        title: 'n im',
        dataIndex: 'toGrade',
        key: 'toGrade',
      },
      {
        title: 'im cng thang 10',
        dataIndex: 'gradeIn10',
        key: 'gradeIn10',
      },
      {
        title: 'im cng thang 4',
        dataIndex: 'gradeIn4',
        key: 'gradeIn4',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#/converted/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Quy i im rn luyn">
        <CartHeadAction>
          <BtnAddNew href="#/converted/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getConvertedPointsPractices}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ConvertedTrainingPointsPage />
  </Page>
);



